import { IODataBasePermission } from '../odataTypes/IODataBasePermission';
/**
 * This class can be used to determine if the current user has a requested set of permissions.
 * Specifies the built-in permissions available in SharePoint Foundation
 * Derived from OneDriveWeb/ODBNext/odsp-shared
 * https://msdn.microsoft.com/en-us/library/microsoft.sharepoint.spbasepermissions.aspx
 * @public
 */
export default class SPPermission {

  // TODO Task 164817 Change public static getters to readonly (TypeScript 2.0)
  /**
   * Has no permissions on the Web site. Not available through the user interface.
   */
  public static get emptyMask(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x0 }); }

  // list/document perms  =0x000000000000XXXX
  /**
   * View items in lists, documents in document libraries, and view Web discussion comments.
   */
  public static get viewListItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x1 }); }

  /**
   * Add items to lists, add documents to document libraries, and add Web discussion comments.
   */
  public static get addListItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x2 }); }

  /**
   * Edit items in lists, edit documents in document libraries, edit Web discussion comments in documents,
   * and customize web part Pages in document libraries.
   */
  public static get editListItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x4 }); }

  /**
   * Delete items from a list, documents from a document library, and Web discussion comments in documents.
   */
  public static get deleteListItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x8 }); }

  /**
   * Approve a minor version of a list item or document.
   */
  public static get approveItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x10 }); }

  /**
   * View the source of documents with server-side file handlers.
   */
  public static get openItems(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x20 }); }

  /**
   * View past versions of a list item or document.
   */
  public static get viewVersions(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x40 }); }

  /**
   * Delete past versions of a list item or document.
   */
  public static get deleteVersions(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x80 }); }

  /**
   * Discard or check in a document which is checked out to another user.
   */
  public static get cancelCheckout(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x100 }); }

  /**
   * Create, change, and delete personal views of lists.
   */
  public static get managePersonalViews(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x200 }); }

  /**
   * Create and delete lists, add or remove columns in a list, and add or remove public views of a list.
   */
  public static get manageLists(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x800 }); }

  /**
   * View forms, views, and application pages, and enumerate lists.
   */
  public static get viewFormPages(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x1000 }); }

  // web level perms      =0x0000XXXXXXXX0000,
  /**
   * Allow users to open a Web site, list, or folder to access items inside that container.
   */
  public static get open(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x20000 }); }

  /**
   * View pages in a Web site.
   */
  public static get viewPages(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x20000 }); }

  /**
   * View the layouts page?
   */
  public static get layoutsPage(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x21000 }); }

  /**
   * Add, change, or delete HTML pages or web part Pages, and edit the Web site using a SharePoint
   * Foundationâ€“compatible editor.
   */
  public static get addAndCustomizePages(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x40000 }); }

  /**
   * Apply a theme or borders to the entire Web site.
   */
  public static get applyThemeAndBorder(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x80000 }); }

  /**
   * Apply a style sheet (.css file) to the Web site.
   */
  public static get applyStyleSheets(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x100000 }); }

  /**
   * View reports on Web site usage.
   */
  public static get viewUsageData(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x200000 }); }

  /**
   * Create a Web site using Self-Service Site Creation.
   */
  public static get createSSCSite(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x400000 }); }

  /**
   * Create subsites such as team sites, Meeting Workspace sites, and Document Workspace sites.
   */
  public static get manageSubwebs(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x800000 }); }

  /**
   * Create a group of users that can be used anywhere within the site collection.
   */
  public static get createGroups(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x1000000 }); }

  /**
   * Create and change permission levels on the Web site and assign permissions to users and groups.
   */
  public static get managePermissions(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x2000000 }); }

  /**
   * Enumerate files and folders in a Web site using Microsoft Office SharePoint Designer 2007 and WebDAV interfaces.
   */
  public static get browseDirectories(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x4000000 }); }

  /**
   * View information about users of the Web site.
   */
  public static get browserUserInfo(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x8000000 }); }

  /**
   * Add or remove personal web parts on a web part Page.
   */
  public static get addDelPrivateWebParts(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x10000000 }); }

  /**
   * Update web parts to display personalized information.
   */
  public static get updatePersonalWebParts(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x20000000 }); }

  /**
   * Grant the ability to perform all administration tasks for the Web site as well as manage content.
   *  Activate, deactivate,or edit properties of Web site scoped Features through the object model or
   * through the user interface (UI). When grantedon the root Web site of a site collection, activate,
   * deactivate, or edit properties of site collection scoped Features through the object model. To
   * browse to the Site Collection Features page and activate or deactivate site collection scoped
   * Features through the UI, you must be a site collection administrator.
   */
  public static get manageWeb(): SPPermission { return new SPPermission({ High: 0x0, Low: 0x40000000 }); }

  /**
   * Use features that launch client applications; otherwise, users must work on documents locally and upload changes. 
   */
  public static get useClientIntegration(): SPPermission { return new SPPermission({ High: 0x10, Low: 0x0 }); }

  /**
   * Use SOAP, WebDAV, or Microsoft Office SharePoint Designer 2007 interfaces to access the Web site.
   */
  public static get useRemoteAPIs(): SPPermission { return new SPPermission({ High: 0x20, Low: 0x0 }); }

  /**
   * Manage alerts for all users of the Web site.
   */
  public static get manageAlerts(): SPPermission { return new SPPermission({ High: 0x40, Low: 0x0 }); }

  /**
   * Create e-mail alerts.
   */
  public static get createAlerts(): SPPermission { return new SPPermission({ High: 0x80, Low: 0x0 }); }

  /**
   * Allows a user to change his or her user information, such as adding a picture.
   */
  public static get editMyUserInfo(): SPPermission { return new SPPermission({ High: 0x100, Low: 0x0 }); }

  /**
   * Enumerate permissions on the Web site, list, folder, document, or list item.
   */
  public static get enumeratePermissions(): SPPermission { return new SPPermission({ High: 0x40000000, Low: 0x0 }); }

  /**
   * Has all permissions on the Web site. Not available through the user interface.
   */
  public static get fullMask(): SPPermission { return new SPPermission({ High: 0x7FFFFFFF, Low: 0xFFFFFFFF }); }

  private _value: IODataBasePermission;

  constructor(value: IODataBasePermission) {
    if (!value) {
      throw new Error('Must use an object with a Low and High number value');
    }
    this._value = value;
  }

  /**
   * Returns the value of this SPPermission object
   */
  public get value(): IODataBasePermission {
    return this._value;
  }

  /**
   * Function for determining if a given permission mask has any of the requested permissions.
   * @param requestedPerms - Any number of SPPermission objects to be compared against the original
   */
  public hasAnyPermissions(...requestedPerms: SPPermission[]): boolean {
    return (requestedPerms || []).some((permission: SPPermission) => {
      return this.hasPermission(permission);
    });
  }

  /**
   * Function for determining if a given permission mask has all of the requested permissions.
   * @param requestedPerms - Any number of SPPermission objects to be compared against the original
   */
  public hasAllPermissions(...requestedPerms: SPPermission[]): boolean {
    return (requestedPerms || []).every((permission: SPPermission) => {
      return this.hasPermission(permission);
    });
  }

  /**
   * Function for checking if a given permission mask has the requested permission.
   * @param requestedPerm - The SPPermission object to be compared against the original
   */
  public hasPermission(requestedPerm: SPPermission): boolean {
    /* tslint:disable:no-bitwise */
    return requestedPerm &&
      ((requestedPerm.value.Low & this.value.Low) === requestedPerm.value.Low) &&
      ((requestedPerm.value.High & this.value.High) === requestedPerm.value.High);
   /* tslint:enable:no-bitwise */
  }
}